import React from "react"
import Layout from "../../components/Layout"
import { graphql, Link } from "gatsby"

export default function Blog({ data }) {
  console.log(data)
  const blogs = data.allMarkdownRemark.nodes
  
  /*return (
    <div>
      <Layout>
        <div className="container mx-auto px-4 md:px-20 pt-28 pb-24">
          <h1 className="text-5xl font-bold">PERSONAL BLOG</h1>
        </div>
      </Layout>
    </div>
  ) */
  
  return (
    <div>
      <Layout>
        <div className="container mx-auto px-4 md:px-20 pt-28 pb-24">
          <div className="text-5xl font-bold">PERSONAL BLOG</div>
          <div className="text-3xl">You can read my personal blogs here</div>
          <div className="pt-16 grid grid-cols-1 md:grid-cols-3 gap-8">
            {blogs.map(blog => (
			  <Link to={"/blog/" + blog.frontmatter.slug} key={ blog.id }>
			    <div className="duration-300">
			      <div className="thumbnail rounded-2xl overflow-hidden">
					<img src= { blog.frontmatter.thumbnail } alt="C/O https://placeholder.com/" className="w-full h-auto" />
				  </div>
			      <div className="pt-4">
					<h1 className="text-2xl font-semibold hover:text-red-500 m-0">{ blog.frontmatter.title }</h1>
					<p className="text-lg">{ blog.frontmatter.stack }</p>
			      </div>
			    </div>
			  </Link>
			))}
          </div>
        </div>
      </Layout>
    </div>
  )
}

//export page query
export const query = graphql`
query BlogPage {
  allMarkdownRemark(
    filter: {fileAbsolutePath: {glob: "**/src/blog/*.md"}}
  ) {
    nodes {
      frontmatter {
        slug
        stack
        title
        thumbnail
      }
      id
    }
  }
}
`
